/**
 * Temporary home for utilities relating to reading/setting the view and associated URL operations
 */

import { Matrix4, Euler, MathUtils, Vector3 } from 'three'
import { parseView, serializeView } from './ViewEncodingUtils.js';

const NANOMETER_TO_WORLD = 10;
const WORLD_TO_NANOMETER = 1 / NANOMETER_TO_WORLD;
const ANGSTROM_TO_WORLD = 1;
const WORLD_TO_ANGSTROM = 1 / ANGSTROM_TO_WORLD;

// proxy object for global access to jolecule with error checking an type injection
const jolecule = {

  __check () {
    if (!window.AQUARIA || !window.AQUARIA.panel3d || !window.AQUARIA.panel3d.embededJolecule) throw new Error('Tried to access Jolecule widget before it was available');
  },

  /**
   * Grab soup view
   * @returns {import('jolecule/src/soup-view').SoupView}
   */
  get view () { this.__check(); return window.AQUARIA.panel3d.embededJolecule.soupView },

  /**
   * Grab soup data from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup').Soup}
   */
  get soup () { this.__check(); return window.AQUARIA.panel3d.embededJolecule.soup },

  /**
   * Grab view controller from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup-view').SoupViewController}
   */
  get viewController () { this.__check(); return window.AQUARIA.panel3d.embededJolecule.controller },

  /**
   * Grab view controller from Jolecule
   * @TODO remove global reference
   * @returns {import('jolecule/src/soup-widget').SoupWidget}
   */
  get soupWidget () { this.__check(); return window.AQUARIA.panel3d.embededJolecule.soupWidget }
}

/**
 * @returns {{ [chain: string]: number[] }}
 */
function getCurrentSelection () {
  const soup = jolecule.soup
  const selection = {}
  const residue = soup.getResidueProxy()
  for (let iRes = 0; iRes < soup.getResidueCount(); iRes++) {
    residue.load(iRes)
    if (residue.selected) {
      if (!selection[residue.chain]) {
        selection[residue.chain] = []
      }
      selection[residue.chain].push(residue.resNum)
    }
  }
  return selection;
}

function getCurrentOrientation () {
  const currentView = jolecule.view.currentView;
  const forward = currentView.cameraParams.position.clone().sub(currentView.cameraParams.focus).normalize()
  const up = currentView.cameraParams.up
  const side = up.clone().cross(forward)
  const up2 = forward.clone().cross(side)
  const rot = new Matrix4()
  rot.set(side.x, up2.x, forward.x, 0,
    side.y, up2.y, forward.y, 0,
    side.z, up2.z, forward.z, 0,
    0, 0, 0, 1)
  const [pitch, yaw, roll] = new Euler().setFromRotationMatrix(rot, 'YXZ').toArray().map(v => MathUtils.radToDeg(v));
  return { pitch, yaw: yaw - 180, roll }
}

function getCurrentZoom () {
  return { value: jolecule.view.currentView.cameraParams.zoom * WORLD_TO_ANGSTROM, units: 'A' } // A only for now
}

export function getCurrentView () {
  return {
    selection: getCurrentSelection(),
    orientation: getCurrentOrientation(),
    zoom: getCurrentZoom()
  }
}

/**
 * @param {{ [chain: string]: number[] }} [selection]
 * @param {{ pitch: number, yaw: number, roll: number }} [orientation]
 * @param {{ value: number, units: 'nm'|'A'|'%' }} [zoom]
 */
export function setCurrentView (selection, orientation, zoom) {
  const soup = jolecule.soup;
  const soupView = jolecule.view;
  const soupController = jolecule.viewController;

  // apply selection (if any, if none, current selection will be retained)
  if (selection) {
    soupController.clearSelectedResidues();
    let proxies = [];
    for (const chain in selection) {
      proxies = proxies.concat(selection[chain].map((res) => soup.findFirstResidue(chain, res)));
    }
    if (proxies[0]) {
      proxies.forEach((proxy) => {
        soupController.selectResidue(proxy.iRes, true);
      });
    }
  }

  // default zoom and orientation based on selection
  const selectionView = soupView.getZoomedOutViewOfSelection();
  let zoomWorldUnits = selectionView.cameraParams.zoom;
  let rotation = new Euler(0, 0, 0);

  // prepare orientation (if any)
  if (orientation) {
    rotation = new Euler(MathUtils.degToRad(orientation.pitch), MathUtils.degToRad(orientation.yaw + 180), MathUtils.degToRad(orientation.roll), 'YXZ');
  }

  // prepare zoom (if any, understands nm only for now)
  if (zoom) {
    switch (zoom.units) {
      case 'nm':
        zoomWorldUnits = zoom.value *= NANOMETER_TO_WORLD;
        break;
      case 'A':
        zoomWorldUnits = zoom.value *= ANGSTROM_TO_WORLD;
        break;
      case '%':
        zoomWorldUnits *= zoom.value / 100;
        break;
      default:
        console.warn(`Unrecognised zoom units: ${zoom.units}`);
        zoomWorldUnits = zoom.value;
        break;
    }
  }

  // apply orientation/zoom
  const newView = soupView.currentView.clone();
  const focus = selectionView.cameraParams.focus;
  const position = focus.clone().add(new Vector3(0, 0, zoomWorldUnits).applyEuler(rotation));
  const up = new Vector3(0, 1, 0).applyEuler(rotation);
  newView.cameraParams.focus.copy(focus);
  newView.cameraParams.up.copy(up);
  newView.cameraParams.position.copy(position);
  newView.cameraParams.zoom = zoomWorldUnits;
  soupView.setTargetView(newView); // use setTargetView to animate to the new view or setCurrentView to immediately snap to the new view
}

/**
 * Update URL with the current view information
 */
export function saveToUrl () {
  const fragment = serializeView(getCurrentView());

  // @TODO set through router instead
  console.debug(`Setting view fragment: ${fragment}`);
  history.replaceState(null, null, `${window.location.origin}${window.location.pathname}${window.location.search}#${fragment}`);
}

/**
 * Load and apply view information from the current URL
 */
export function loadFromUrl () {

  // @TODO access through router instead (parse view at router level)
  const fragment = location.hash;
  if (fragment === '') return; // nothing to load
  console.debug(`Loading view fragment: ${fragment}`);

  const { selection, orientation, zoom } = parseView(fragment);
  setCurrentView(selection, orientation, zoom);
}

/**
 * Grab the current raw camera pose with optional handedness conversion
 * @param {Boolean} convertToLHS
 * @returns {{ position: Number[], orientation: Number[] }}
 */
export function getRawCameraPose (convertToLHS = true) {
  const position = getSoupWidget().camera.position.clone()
  const orientation = getSoupWidget().camera.quaternion.clone()
  if (convertToLHS) {
    position.x = -position.x
    orientation.set(orientation.z, orientation.w, orientation.x, orientation.y)
  }
  return {
    position: [position.x, position.y, position.z],
    orientation: [orientation.x, orientation.y, orientation.z, orientation.w]
  }
}

/**
 * Grab high level view data
 */
export function getView () {
  return jolecule.view;
}

export class ViewWatcher extends EventTarget {
  constructor() {
    super();
    this.latestPose = null;
    this.latestView = null;
    this._update();
  }

  _update() {
    requestAnimationFrame(() => this._update());
    try {
      const view = getView()
      const pose = getRawCameraPose()
      let viewChanged = false
      if (!this.latestView || ! this.latestPose) {
        viewChanged = true;
      }
      else {
        for (let i = 0; i < 3; i++) if (this.latestPose.position[i] !== pose.position[i]) { viewChanged = true; break }
        for (let i = 0; i < 4; i++) if (this.latestPose.orientation[i] !== pose.orientation[i]) { viewChanged = true; break }
      }
      if (viewChanged) {
        this.latestPose = pose
        this.latestView = view
        this.dispatchEvent(new CustomEvent('viewchange', { detail: { view, pose } }))
      }
    } catch {}
  }
}
